
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import Divider from '@/components/divider/divider'
import IndexList from '@/components/index-list/index-list'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import SearchHeader from '@/components/seach-header/seach-header'
import Waterfall from '@/components/waterfall/waterfall'
import TWEEN from '@tweenjs/tween.js'

import icon from '@/assets/json/addressIcon.json'
import defaultAvatar from '@/assets/img/default_avatar.png'

import getWeather from '@/api/getWeather'
import { isIphoneX } from '@/assets/js/brower.js'
import axios from '@/api/axiosApi'
import { mapGetters, mapMutations } from 'vuex'
import { QINIU_PREFIX } from '@/api/config'
import { localUser } from '@/assets/js/local'

export default {
name: 'Address',
data () {
return {
icon,
clickBack: false,
showLasted: false,
showSearch: false,
showAtlas: false,
showFutureUser: false,
showUserList: false,
showSelectAddress: false,
opacity: 0,
featureWidth: 500,
activeMarqueeIndex: 0,
marqueeAnimate: false,
seachValue: '',
weather: {
weather: '',
temperature: 12
},
atlasNum: 0,
recommendRate: 30,
recommendRateCopy: 30,
user: null,
userList: [],   // 
futureUserList: [],   // 
productList: [],  // 
pushList: [],   // 
searchList: [],   // 
localImgs: [],    // 6
atlas: [],        // 
messageRecord: [], // 
travelsList: []
}
},
computed: {
itemWidth () {
return (window.innerWidth - 21) / 2
},
marqueeTop () {
return -this.activeMarqueeIndex * 51 + 'px'
},
...mapGetters([
'currentArea', 'areaList'
])
},
directives: {
TransferDom
},
components: {
VHeader, Scroll, Marquee, MarqueeItem, Divider, Scroller, Waterfall, Popup, SearchHeader, IndexList, Grid, GridItem
},
created () {
this.paddingBottom = 0
if (isIphoneX()) {
this.paddingTop = 40 + 34
this.atlasScrollHeight = window.innerHeight - (40 + 34)
this.paddingBottom = 20
} else {
this.paddingTop = 40 + 20
this.atlasScrollHeight = window.innerHeight - (40 + 20)
}

this.dynamicWidth = (window.innerWidth - 30) / 2 - 10
this.showSearch = false


},
activated () {

this.user = localUser.get()
this._getAreaInfo(this.currentArea.id)

this.startMarquee(3500)
},
deactivated() {
this.stopMarquee()
},
destroyed () {
this.stopMarquee()
},
methods: {
_getAreaInfo (id) {
const _self = this
axios.get(this, `/v1/addressInfo/${id}`, null, (data) => {
_self.setCurrentArea(data)
getWeather(_self.currentArea.name, params => {
this.weather = params
})
_self._getPlaceOrder()
_self._getProductList()
_self._getRecommendRate()
_self._getUserList()
_self._getFutureUserList()
_self._getAtlas(true)
_self._getAtlasNum()
_self._getPlayStrategy()
}, null, false)
},
_getAtlasNum () {
const _self = this
axios.get(this, `/v1/atlas/${this.currentArea.id}/no`, null, (data) => {
_self.atlasNum = data
}, null, false)
},
_getUserList () {
const _self = this
axios.get(this, `/v1/users`, {
areaIs: this.currentArea.id,
startTime: this.moment().subtract(3, 'days').format('YYYY-MM-DD'),
endTime: this.moment().format('YYYY-MM-DD'),
pageSize: 20
}, (data) => {
data.forEach(item => {
let tmpHeadUrl = defaultAvatar
if (item.headUrl) {
if (item.headUrl.indexOf(QINIU_PREFIX) > -1) {
tmpHeadUrl = item.headUrl + '-bheader'
} else {
tmpHeadUrl = item.headUrl
}
}
item.headUrl = tmpHeadUrl
})
_self.userList = data
}, null, false)
},
_getFutureUserList () {
const _self = this
axios.get(this, `/v1/users`, {
areaIs: this.currentArea.id,
startTime: this.moment().format('YYYY-MM-DD'),
endTime: this.moment().add(1, 'days').format('YYYY-MM-DD'),
pageSize: 20
}, (data) => {
data.forEach(item => {
let tmpHeadUrl = defaultAvatar
if (item.headUrl) {
if (item.headUrl.indexOf(QINIU_PREFIX) > -1) {
tmpHeadUrl = item.headUrl + '-bheader'
} else {
tmpHeadUrl = item.headUrl
}
}
item.headUrl = tmpHeadUrl
})
_self.futureUserList = data
}, null, false)
},
_getPlaceOrder () {
const _self = this
axios.get(this, `/v1/recommendProductComment/${this.currentArea.id}`, {
pageSize: 20
}, (data) => {
data.forEach((item) => {
if (item.headUrl == null || item.headUrl === '') {
item.headUrl = defaultAvatar
}
})
_self.pushList = data
_self.$nextTick(() => {
_self.startMarquee(3500)
})
}, null, false)
},
_getProductList () {
axios.get(this, '/v1/product', {
recommend: 1,
areaId: this.currentArea.id
}, (data) => {
data.forEach(item => {

})
this.featureWidth = data.length * (145 + 13)
this.productList = data
})
},
_getRecommendRate () {
axios.get(this, `/v1/travelScore/${this.currentArea.id}`, null, (data) => {
this.recommendRate = data
}, null, false)
},
_getAtlas (isFirst) {
axios.get(this, `/v1/atlas/${this.currentArea.id}/yes`, null, (data) => {
let tmp = []
data.forEach(item => {
let w200 = null
if (item.indexOf(QINIU_PREFIX) > -1) {
w200 = item + '-w200'
}
tmp.push({
src: item,
w200: w200
})
})
if (isFirst) {
this.localImgs = tmp
} else {
this.atlas = tmp
}
})
},
_getMessageRecord() {
const userId = localUser.get('id')
if (userId == null || userId === '') {
return
}
axios.get(this, `/v1/messageRecords/${userId}`, {
endTime: this.moment().subtract(7, 'days').format('YYYY-MM-DD'),
pageSize: 50
}, (data) => {
let today = []
let nearlyThree = []
let lastDays = []
data.forEach(item => {
if (this.moment(item.updateAt).format('YYYY-MM-DD') === this.moment().format('YYYY-MM-DD')) {
today.push(item)
} else if (this.moment(item.updateAt).isAfter(this.moment().subtract(3, 'days').format('YYYY-MM-DD'))) {
nearlyThree.push(item)
} else {
lastDays.push(item)
}
})
this.messageRecord = [
today,
nearlyThree,
lastDays
]
})
},
_getPlayStrategy () {
axios.get(this, '/v1/playStrategy', {
publishAddressId: this.currentArea.id
}, (data) => {
this.travelsList = data
})
},
scroll (position, movingY) {
if (position.y <= -230) {
this.opacity = 1
} else {
this.opacity = Math.abs(position.y / 230)
}
},
startMarquee (time) {
this.stopMarquee()
this.marqueeTimer = setInterval(() => {
if (this.activeMarqueeIndex < (this.pushList.length - 4)) {
this.activeMarqueeIndex ++
this.marqueeAnimate = true
} else {
this.activeMarqueeIndex = 0
this.marqueeAnimate = false
}
}, time)
},
stopMarquee () {
if (this.marqueeTimer) {
clearInterval(this.marqueeTimer)
this.marqueeTimer = null
}
},
toMap (type) {
this.$router.push(`/address/map/${this.currentArea.id}/${type}`)
},
toProductDetail (id, type) {
this.$router.push(`/cart/product/${id}/${type}`)
},
toDynamicDesc (item) {
this.$router.push(`/meiTu/dynamicDesc/${item.id}`)
},
toAtlas () {
this.showAtlas = true
this._getAtlas()
},
toPlayStrategy () {
this.$router.push('/address/playStrategyList')
},
toPlayStrategyDetail (id) {
this.$router.push('/address/playStrategy/' + id)
},
submitSearch (value) {
axios.get(this, `/v1/product`, {
name: value,
type: this.currentArea.type,
pageSize: 10
}, (data) => {
this.searchList = data
})
},
clickSearchItem (item) {
this.toProductDetail(item.id, item.type)
this.showSearch = false
this.seachValue = ''
this.searchList = []
},
clickRecordItem (id, type) {
this.toProductDetail(id, type)
this.showLasted = false
},
toUSerDetail (userId) {
this.$router.push(`/user/archives/${userId}`)
},
toLasted () {
this.showLasted = true
this._getMessageRecord()
},
toContent() {
this.$router.push(`/content/${this.currentArea.contentId}/${'-' + this.currentArea.name}`)
},
selectArea (area) {
this.setCurrentArea(area)
this.showSelectAddress = false
this._getAreaInfo(this.currentArea.id)
},
...mapMutations({
setCurrentArea: 'SET_CURRENT_AREA'
})
},
watch: {
recommendRate (newValue, oldValue) {
var vm = this
function animate () {
if (TWEEN.update()) {
requestAnimationFrame(animate)
}
}
const tmp = { tweeningNumber: oldValue }
new TWEEN.Tween(tmp)
.easing(TWEEN.Easing.Quadratic.Out)
.to({ tweeningNumber: newValue }, 1800)
.onUpdate(function () {
vm.recommendRateCopy = tmp.tweeningNumber.toFixed(2)
})
.start()
animate()
}
}
}



