import Slider from '@/components/slider/slider'
import Scroll from '@/components/scroll/scroll'
import { isIphoneX } from '@/assets/js/brower'
import animations from 'create-keyframe-animation'
import axios from '@/api/axiosApi'
import homeList from '@/assets/json/homeList.json'
import bannerList from '@/assets/json/bannerAddress.json'
import AITag from '@/assets/img/AI_tag.png'
import icon from '@/assets/json/homeIcon.json'
import nativeBridge from '@/assets/js/nativeBridge'
import { mapGetters, mapMutations } from 'vuex'
import { getDistance } from '@/assets/js/amap'

// Area
const AreaItemHeight = 138
const MarginBottom = 15
let TopHeight = 223
const BottomHeight = 50

export default {
  name: 'Home',
  data () {
    return {
      startIndex: 0,
      endIndex: 3,
      bannerList: bannerList,
      homeList,
      addressList: [],
      AITag,
      icon
    }
  },
  components: {
    Slider, Scroll
  },
  computed: {
    ...mapGetters([
      'position', 'areaList', 'currentArea'
    ])
  },
  created () {
    this.probeType = 3
    this.listenScroll = true
    this.sliderHeight = 210 + 20
    this.paddingBottom = 0
    if (isIphoneX()) {
      this.sliderHeight += 14
      this.paddingBottom = 44
    }
    TopHeight = this.sliderHeight - 45
    this._getBanner()
    this._getAddressList()

    const _self = this
    nativeBridge.getPosition(this, (arg) => {
      _self.setPosition([parseFloat(arg.longitude), parseFloat(arg.latitude)])
    })
  },
  mounted () {
    this.endIndex = Math.floor((window.innerHeight - TopHeight - BottomHeight) / (AreaItemHeight + MarginBottom))
  },
  methods: {
    _getBanner () {
      axios.get(this, '/v1/banner/0', null, (data) => {
        data.forEach(item => {
          item.thumbUrl += '-banner'
        })
        this.bannerList = data
      }, null, false)
    },
    loadImage () {
      if (!this.checkloaded) {
        this.checkloaded = true
        this.$refs.scroll.refresh()
      }
    },
    scroll (position) {
      const scrollY = position.y
      if (scrollY < 0) {
        const currentStartIndex = Math.abs(scrollY) <= TopHeight ? 0 : parseInt((Math.abs(scrollY) - TopHeight) / (AreaItemHeight + MarginBottom))
        let currentEndIndex = Math.floor((window.innerHeight - (TopHeight + scrollY) - BottomHeight) / (AreaItemHeight + MarginBottom))
        if (currentEndIndex > this.homeList.length - 1) {
          currentEndIndex = this.homeList.length - 1
        }

        if (currentStartIndex !== this.startIndex) {
          if (currentStartIndex < this.startIndex) {
            this.runAnimation(currentStartIndex)
          }
          this.startIndex = currentStartIndex
        }
        if (currentEndIndex !== this.endIndex) {
          if (currentEndIndex > this.endIndex) {
            this.runAnimation(currentEndIndex)
          }
          this.endIndex = currentEndIndex
        }
      }
    },
    runAnimation (index) {
      animations.registerAnimation({
        name: 'scale',
        animation: [
          {
            scale: 0.5,
            opacity: 0
          },
          {
            scale: 1,
            opacity: 1
          }
        ],
        presets: {
          duration: 300,
          resetWhenDone: true
        }
      })
      animations.runAnimation(this.$refs['area-' + index], 'scale')
    },
    clickBanner (item) {
      // 
      if (item.type === 0 && item.contentId) {
        this.$router.push(`/content/${item.contentId}/Banner`)
      }
    },
    clickItem (id, targetId) {
      switch (id) {
        case '0':
          this.$store.commit('SET_NATIVE_AREA', targetId)
          this.$store.commit('SET_CURRENT_AREA', targetId)
          this.$router.replace(`/address`)
          break
        case '1':
          this.$store.commit('SET_MEITU_TAB', 2)
          this.$router.replace('/meiTu')
          break
        case '2':
          this.$store.commit('SET_MEITU_TAB', 0)
          this.$router.replace('/meiTu')
          break
        case '3':
          this.$router.push(`address/productList/${targetId}/1`)
          break
        case '4':
          this.$router.push(`address/productList/${targetId}/0`)
          break
        case '5':
          this.$vux.toast.text('~')
          break
      }
    },
    toAI () {
      this.$router.push('/chatAI')
    },
    _getAddressList () {
      axios.get(this, `/v1/address/1`, {
        pageSize: 50
      }, (data) => {
        this.addressList = data
        let temp = []
        data.forEach(item => {
          // index-list
          const findIndex = temp.findIndex((o) => o.name === item.nameSpell.toUpperCase()[0])
          if (findIndex < 0) {
            temp.push({
              name: item.nameSpell.toUpperCase()[0],
              items: [item]
            })
          } else {
            temp[findIndex].items.push(item)
          }
        })
        temp.sort((o1, o2) => o1.name.charCodeAt(0) - o2.name.charCodeAt(0))
        this.setAreaList(temp)
      })
    },
    colDistance () {
      if (this.position.length === 2 && this.addressList.length > 0) {
        let min = this.addressList[0]
        this.addressList.forEach((item, index) => {
          item.distance = getDistance(this.position, [item.longitude, item.latitude])
          if (index > 0 && item.distance < this.addressList[index - 1].distance) {
            min = item
          }
        })
        this.homeList[0].title = min.name
        this.homeList[0].targetId = min.id
        this.homeList[0].desc = `WelcomeTo${min.nameSpell}`
        this.homeList[0].thumbUrl = min.thumbUrl
        this.setCurrentArea(min)
      }
    },
    ...mapMutations({
      setPosition: 'SET_POSITION',
      setAreaList: 'SET_AREA_LIST',
      setCurrentArea: 'SET_CURRENT_AREA'
    })
  },
  watch: {
    position () {
      this.colDistance()
    },
    addressList () {
      this.colDistance()
    }
  }
}



